import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { BarcodeScanner, BarcodeScannerOptions, BarcodeScanResult } from '@ionic-native/barcode-scanner';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {

  result: BarcodeScanResult;
  dataToEncode: string;

  constructor(public navCtrl: NavController, public barcode: BarcodeScanner) {

  }

 ngOnInit(){

 }

 async encodeData(){

  try{

     this.dataToEncode = await this.barcode.encode(this.barcode.Encode.TEXT_TYPE, this.dataToEncode);

  }catch (error){
    console.log(error);
  }

 }

 scanBarcode2(){
 console.log("loy");
 }

 async scanBarcode(){

    try{

      const options: BarcodeScannerOptions ={
      prompt:'point camera on the barcode',
      torchOn: true 
      }

     this.result = await this.barcode.scan(options);

     console.log(this.result);


  }catch (error){
    console.log(error);
  }

 }


}



