import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, LoadingController } from 'ionic-angular';
import { ItemsProvider } from '../../providers/items/items';


@IonicPage()
@Component({
  selector: 'page-item',
  templateUrl: 'item.html',
  providers: [ItemsProvider]
})
export class ItemPage {
  code: string;
  dataModel: any;
  info: boolean;



  constructor(public navCtrl: NavController, public navParams: NavParams, public viewCtrl : ViewController, public itemsProvider: ItemsProvider,  public loadingCtrl: LoadingController) {
    
  }

  ionViewDidLoad() {

  //get param

  this.code=this.navParams.get('idNumber'); 

  

    let loading = this.loadingCtrl.create({content : "Loading please wait..."});
    loading.present();


    setTimeout(() => {
      this.doSearch().then(() => {
        loading.dismissAll();
      });
  }, 1000);

  }


  doSearch(): Promise<any>{
   return this.itemsProvider.getRecord().then((data =>{
    this.dataModel = data.filter(task => task.idnumber === this.code);
    console.log(this.dataModel.length);
    if(this.dataModel.length >= 1){
      this.info=false;
    }else{
      this.info=true;
    }
  
    
      console.log();
      //console.log(this.dataModel[0].image);
    }))
  }

  

}



