import { Component } from '@angular/core';
import { ModalController, NavController } from 'ionic-angular';
import { BarcodeScanner, BarcodeScannerOptions, BarcodeScanResult } from '@ionic-native/barcode-scanner';
import { ItemPage } from '../item/item';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  result: BarcodeScanResult;
  dataToEncode: string;
  code: string;

  constructor(public modalCtrl: ModalController, public barcode: BarcodeScanner,public navCtrl: NavController) {

  }
 

  search(){
    //set param for next page
    console.log(this.code);
        this.navCtrl.push(ItemPage, {idNumber: this.code});
  }


  async scanBarcode(){

    try{

      const options: BarcodeScannerOptions ={
      prompt:'Point camera on the barcode utuhkoooo....',
      torchOn: true 
      }

     this.result = await this.barcode.scan(options);
     this.code=this.result.text;
     this.navCtrl.push(ItemPage, {idNumber: this.code});

     console.log(this.result);


  }catch (error){
    console.log(error);
  }

 }

}



